@media only screen and (min-width: 1824px) {
  body {
    height: 100%;
  }
  .page-container {
    height: 100%;
  }
  /** Side bar widget **/
  .footer-widget {
    position: fixed;
  }
  /** Tables **/
  .table th {
    font-size: 14px;
  }
  .table td {
    font-size: 14px;
  }
  .table td.small-cell,
  .table th.small-cell {
    width: 42px;
  }
  /** Controls **/
  .checkbox label:before {
    top: 0.5px;
  }
  /** Tiles **/
  .tiles.margin-reset {
    margin-left: 51px;
  }
  /** Only Demo Purpose **/
  .demo-map {
    min-height: 100%;
  }
  .error-main {
    margin-top: 40%;
  }
  .visible-xlg {
    display: block !important;
  }
  .hidden-xlg {
    display: none !important;
  }
  .cbp_tmtimeline > li .cbp_tmtime {
    width: 24%;
  }
}
@media (min-width: 1600px) {
  .visible-xlg {
    display: block !important;
  }
  .hidden-xlg {
    display: none !important;
  }
}
@media (max-width: 1400px) {
  .sales-graph-heading {
    padding-left: 17px;
    padding-top: 30px;
  }
}
/*** Desktops & Laptops ***/
@media (min-width: 980px) {
  /*** Page sidebar ***/
  body.bottom-header .page-sidebar {
    margin-top: 0;
    margin-bottom: 58px;
  }
  .menu-non-fixed .page-sidebar {
    margin-top: 0;
    padding-top: 58px;
  }
  .page-sidebar {
    display: inline-block;
    float: left;
    position: fixed;
    width: 250px;
    margin-top: 58px;
    padding-bottom: 105px;
  }
  .menu-non-fixed .footer-widget {
    z-index: 100;
  }
  .page-sidebar .page-sidebar-wrapper {
    display: inline-block;
    float: left;
    height: 100%;
    max-width: 250px;
    width: 100%;
  }
  .page-sidebar.fixed {
    position: fixed;
  }
  .page-sidebar ul {
    width: 250px;
  }
  /*** Page content ***/
  .page-content {
    margin-left: 250px;
    min-height: 100%;
  }
  .page-content.no-min-height {
    min-height: auto;
  }
  .full-width-page .page-content {
    margin-left: 0px !important;
  }
  .inner-menu {
    padding-top: 60px;
  }
  /** Const size button for medium devices **/
  .btn-cons-md {
    min-width: 120px;
  }
  .boxed-layout {
    background-color: #f6f7f8;
  }
  .boxed-layout .container {
    position: relative;
  }
  .boxed-layout .chat-window-wrapper {
    position: absolute;
  }
  .boxed-layout .chat-window-wrapper .inner-content .chat-window-wrapper {
    margin-right: 0;
  }
  .horizontal-menu .header-seperation {
    display: none !important;
  }
}
@media screen and (min-width:0\0) {
  body.open-menu-right .page-container {
    position: relative;
    left: -260px;
  }
  body.open-menu-right .header {
    left: -260px;
  }
  body.open-menu-right .footer-widget {
    left: -260px;
  }
  body.open-menu-right .scrollup {
    display: none !important;
  }
  body.open-menu-right .page-content .admin-bar {
    left: -260px;
  }
}
/*** General tablets and phones ***/
@media (max-width: 979px) {
  /*** Body ***/
  body {
    margin: 0px !important;
  }
  .horizontal-menu .page-content {
    margin-left: 0px;
  }
  /*** Page header ***/
  .header {
    margin: 0 !important;
  }
  .header .nav li.dropdown i {
    display: inline-block;
    position: relative;
    top: 1px;
    right: 0px;
  }
  .header .nav {
    margin-bottom: 0px !important;
  }
  /*** Page container ***/
  .page-container {
    margin: 0 !important;
    padding: 0 !important;
  }
  .fixed-top .page-container {
    margin-top: 0px !important;
  }
  /*** Page content ***/
  .page-content {
    margin-top: 0;
  }
  .chat-window-wrapper {
    -webkit-overflow-scrolling: touch;
  }
  .chat-messages {
    -webkit-overflow-scrolling: touch;
  }
  .tiles {
    margin-right: 0px;
  }
  .tiles.added-margin {
    margin-right: 0px;
  }
  .page-content .admin-bar .admin-bar-inner {
    margin-right: 0px;
  }
  .scrollup {
    left: 235px;
  }
  body.condense-menu .page-content {
    margin-left: 0;
  }
}
/* Landscape view of all tablet devices */
@media (min-width: 768px) and (max-width: 1024px) {
  .horizontal-menu .page-content {
    margin-left: 0px;
  }
  .horizontal-menu .page-content .content {
    margin-top: 26px;
  }
  /*** Body ***/
  body {
    background-color: #0aa699;
    padding-top: 0px;
  }
  .page-sidebar.opened {
    background-color: #1b1e24 !important;
    height: auto;
    font-family: 'Open Sans';
    width: 250px;
    margin-top: 60px;
  }
  .page-sidebar.opened .outer-tab-nav-section {
    display: inline-block;
    width: 45px;
    position: absolute;
    height: 100%;
    background-color: #0aa699 !important;
  }
  .page-sidebar.opened .inner-menu {
    display: inline-block;
    width: 200px;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li > a {
    color: #8b91a0;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li:last-child > a {
    border-bottom: 1px solid transparent !important;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li a i {
    color: #8b91a0;
    width: 20px;
    text-align: center;
    display: inline-block;
    margin-right: 10px;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li.open > a,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li > a:hover,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li:hover > a {
    background: #1b1e24;
    /* border-left:4px solid  #0aa699;*/
    padding: 11.5px 31px;
    color: #fff;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li.open > a i,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li > a:hover i,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li:hover > a i {
    color: #fff;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li.active > a {
    border-top-color: transparent !important;
    color: #fff;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li.active i {
    color: #fff;
  }
  .page-sidebar.opened .page-sidebar-wrapper > ul > li > a.open {
    background: #313131;
  }
  .page-sidebar.opened .page-sidebar-wrapper ul > li > a > .arrow:before,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li > a > .arrow.open:before {
    color: #919091 !important;
  }
  .page-sidebar.opened .page-sidebar-wrapper ul > li.active > a .arrow:before,
  .page-sidebar.opened .page-sidebar-wrapper > ul > li.active > a .arrow.open:before {
